.nav-main{
    width: 100%;
    height: 56px;
    position: relative;
    z-index: 10;
}

.nav-list{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__item{    
        list-style: none
    }
    
    &__link{
        display: flex;
        align-items: center;
    }

